.content {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px 0;
}

.post {
  max-width: 300px;
  flex: 1 1 0;
  border: 1px solid #0000001a;
  border-radius: 8px;
  box-shadow: 0 10px 10px #0000001a;
  padding: 20px;
}

.title {
  font-size: 18px;
  font-weight: 700;
  margin: 0 0 10px 0;
}